import { Routes, Route, Navigate } from "react-router-dom";
import HeaderNav from "./components/Layout/HeaderNav";
import Footer from "./components/Layout/Footer";
import LandingPage from "./components/LandingPage";
import Proyectos from "./components/Proyectos";
import Curriculum from "./components/Curriculum";
import Contact from "./components/Contact";
import Project from "./components/Project";

function App() {
  return (
    <>
      {/* HEADER AND NAVIGATION */}
      <HeaderNav />
      {/* MAIN CONTENT (BY ROUTES) */}
      <Routes>
        <Route path="/" element={<Navigate to="/portfolio/index" />} />
        <Route path="/portfolio" element={<LandingPage />} />
        <Route path="/portfolio/proyectos" element={<Proyectos />} />
        <Route path="/portfolio/curriculum" element={<Curriculum />} />
        <Route path="/portfolio/contacto" element={<Contact />} />
        <Route path="/portfolio/proyecto/:id" element={<Project />} />
        <Route path="*" element={<h1 className="heading">Error 404</h1>} />
      </Routes>
      {/* FOOTER */}
      <Footer />
    </>
  );
};

export default App;