import "../assets/css/LandingPage.css";
import { Link } from "react-router-dom";
import JobsListing from "./JobsListing";
import { ListGroup } from "react-bootstrap";
import { hover } from "@testing-library/user-event/dist/hover";

const LandingPage = () => {
  return (
    <main className="LandingPage">
      <h1 className="heading">
        Bienvenido, mi nombre es <strong>Pablo Surez</strong>,
        soy <strong>Desarrollador Web</strong> y <strong>Tcnico en Sistemas Microinformticos y Redes</strong>,
        ofrezco mis servicios de <strong>programacin</strong>, <strong>desarrollo</strong> e <strong>informtica</strong> en
        todo tipo de proyectos.
      </h1>
      <span id="help__you">
        Te ayudo a crear tu sitio o aplicacin web, realizar cualquier tipo de instalacin,
        reparacin o mantenimiento tanto de hardware, software y Redes.
      </span>
      <p id="contact__with--me"><Link to="/portfolio/contacto">Contacta conmigo</Link></p>
      <section className="my__projects">
        <h2>Tecnologas que implemento en el Desarrollo Web.</h2>
        <ul className="tech__list">
          <li>
            <h3>Frontend</h3>
            <ListGroup>
              <ListGroup.Item className="border">HTML5</ListGroup.Item>
              <ListGroup.Item className="border">CSS3, SASS, BOOTSTRAP5</ListGroup.Item>
              <ListGroup.Item className="padding">JavaScript, React, TypeScript, jQuery</ListGroup.Item>
            </ListGroup>
          </li>
          <br />
          <li>
            <h3>Backend</h3>
            <ListGroup>
              <ListGroup.Item className="border">Node, Express, PHP (POO, MVC), Laravel</ListGroup.Item>
              <ListGroup.Item className="padding">MongoDB, MySQL/MariaDB, SQL</ListGroup.Item>
            </ListGroup>
          </li>
          <br />
          <li>
            <h3>Gestores de contenido</h3>
            <ListGroup>
              <ListGroup.Item className="border">WordPress</ListGroup.Item>
            </ListGroup>
          </li>
        </ul>
        <h2 className="landing__projects">
          Aqu podras encontrar un par de <strong>proyectos</strong>, para ver ms clica 
            <Link to="/portfolio/proyectos" className="btn" style={{ color: "#fa4529" }}>aqu</Link>.
        </h2>
        <JobsListing limit={2} />
      </section>
    </main>
  );
};

export default LandingPage;